import React,{ useState,useEffect } from 'react'
import LogoUrl from './logo.svg'
import { NavLink,useHistory } from 'react-router-dom'
import styled from 'styled-components'
import 'antd/dist/antd.css'
import { Button } from 'antd'
import { useStores } from '../stores'
import { observer } from 'mobx-react'  // 

const Header=styled.header`
    padding:10px 100px;
    display:flex;
    align-items:center;
    background-color:#02101f;
    color:#fff;
`;
const Logo=styled.img`
    height:30px;
`;
const StyledLink=styled(NavLink)`
    color:#fff;
    margin-left:30px;

    &.active{
        border-bottom:1px solid #fff;
    }
`;
const Login=styled.div`
    margin-left:auto
`;
const StyledButton=styled(Button)`
    margin-left:10px;
`
const Component=observer(()=>{
    const {UserStore,AuthStore}=useStores()   // 
    const history=useHistory()  // history.push('/XXX') xxx

    const handleLogout=()=>{
        AuthStore.logOut()
    }
    const handleLogin=()=>{
        console.log('')
        history.push('/login')
    }
    const handleRegister=()=>{
        console.log('')
        history.push('/register')
    }
    useEffect(()=>{
        UserStore.pullUser();
    },[])
    
    return (
        <Header>
            <Logo src={LogoUrl}/>
            <nav>
                {/* activeClassName: ,classactive */}
                <StyledLink to="/" activeClassName='active' exact></StyledLink>
                <StyledLink to="/history" activeClassName='active'></StyledLink>
                <StyledLink to="/about" activeClassName='active'></StyledLink>
            </nav>
            <Login>
                {/* isLogintrue,false */}
                {/* ,setIsLogintrue => isLogintrue =>  */}
                {/* ,setIsLoginfalse => isLoginfalse =>  */}
                {
                    UserStore.currentUser ? <>
                        {UserStore.currentUser.attributes.username} <StyledButton type="primary" onClick={handleLogout}></StyledButton>
                    </> : <>
                        <StyledButton type="primary" onClick={handleLogin}></StyledButton>
                        <StyledButton type="primary" onClick={handleRegister}></StyledButton> 
                    </>
                }
            </Login>
        </Header>
    )
})

export default Component